
import { defineComponent, PropType } from "vue";

export default defineComponent({
    props: {
        spriteSource: {
            type: String as PropType<string>,
            required: true,
        },
        ballSource: {
            type: String as PropType<string>,
            required: true,
        },
    },
    methods: {
        close() {
            this.$emit("clicked");
        },
    },
});
